<template>
  <div style="overflow: hidden">
    <div>
      <div class="mhead">
        <ul>
          <li>
            <text>无卡收费</text>
          </li>
        </ul>
      </div>
      <div class="body">
        <div class="auto" style="height: 10%">
          <div class="col-xs-9" style="padding-top: 20px">
            <label  class="col-xs-4 control-label" style="font-size:18px" ><nobr>用户编号</nobr></label>
            <div class="col-xs-7">
              <input type="text" class="form-control"  v-model="userCode" placeholder="请输入用户编号"/>
            </div>
          </div>
          <div class="col-xs-3" style="padding-top: 20px">
            <button  type="button" class="btn btn-sm btn-primary"  @click="search()"> 查询 </button>
          </div>
        </div>
        <div class="auto" style="height: 93%;">
          <table class="table table-bordered self-table">
            <thead style="background-color: #499edf">
            <tr class="table-tr-1">
              <th style="text-align:center;"><nobr>用户名称</nobr></th>
              <th style="text-align:center;"><nobr>用户编号</nobr></th>
              <th style="text-align:center;"><nobr>操作</nobr></th>
            </tr>
            </thead>
            <tbody v-for = "row in model.rows">
            <tr>
              <td style="text-align:center;"><p class="name">{{row.f_user_name}}</p></td>
              <td style="text-align:center;"><p class="address">{{row.f_userinfo_code}}</p></td>
              <td style="text-align:center;">
                <button type="button" class="btn btn-primary btn-sm" @click='confirm(row)'>确认</button>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
        <div class="foot" @click="back('tomain')">
          <text>返回</text>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from "vue";
  let getuser = function* (self) {
    let condition = '1 = 1'
    if (Vue.PosUtil.posParam.filiale.hasFiliale) {
      condition = Vue.PosUtil.posParam.filiale.condition + ' and '
    }
    let request = {
      condition : condition + Vue.PosUtil.posParam.userCode + '= \'' + self.userCode+'\' Order by ' + Vue.PosUtil.posParam.userCode
    }
    let result = yield self.$resetpost(Vue.ProxyUrl + '/rs/sql/'+Vue.PosUtil.posParam.revenue+'GetUserInfo', {data: request}, {resolveMsg: null, rejectMsg: null})
    if (result){
      if (result.data.length === 0){
        self.$showMessage("请确认您输入的用户编号是否正确！")
      }
      self.model.rows = result.data
    }
  }
  import co from 'co'
  export default {
    title: '编号查询',
    name: "MachineMain",
    data () {
      return {
        userCode:'0000000003',
        model: {rows: []},
      }
    },
    methods: {
      confirm(row) {
        // 跳转用户详情界面
        this.$goto('user-info',{userInfo:row},'self',this.callBack)
      },
      search(){
        if(this.f_user_name === ''){
          this.$showAlert('请先输入用户编号', 'warning', 1500)
          return
        }
        this.model.rows = []
        co(getuser(this))
      },
      back (param) {
        this.$back(param)
      },
      callBack(param){
        if(param === "toSelect"){
          return
        }
        this.$back(param)
      },
    }
  }
</script>



